<template>
    <div id="app">
        <router-view></router-view>
    </div>
</template>

<script>
    import appConfig from './.app.config'
    import orgScoreStore from './model/orgScoreStore'
    import axios from 'axios'
    export default {
        name: 'app',
        data () {
            return {
                name: appConfig.event.name
            }
        },
        methods: {
            loadScores: async function () {
                console.log(orgScoreStore.state.org.name)
                if (!orgScoreStore.state.org.name) return
                var response = await axios.get(appConfig.resourceServer.scoreUri + '/' + orgScoreStore.state.org.name)
                console.log(response.data)
                if (response.data && response.data.challengeStats && response.data.challengeStats.scores) {
                    orgScoreStore.setScores(response.data.challengeStats.scores)
                }
            }
        },
        mounted: function () {
            setInterval(function () {
                this.loadScores()
            }.bind(this), 10000)
        }
    }
</script>

<style lang="scss">
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;
    }
    .bg-info {
        background-color: #007dc1 !important;
    }
</style>
