<template>
    <div>
        <div>
            <b-navbar toggleable="lg" type="dark" variant="primary">
                <b-navbar-brand href="#">{{name}}</b-navbar-brand>

                <b-navbar-toggle target="nav_collapse" />

                <b-collapse is-nav id="nav_collapse">

                    <b-navbar-nav v-if="scores.READ_ONLY_API_TOKEN">
                        <b-button variant="warning">{{orgName.name}}: <b-badge variant="light">{{orgScore}} points</b-badge></b-button>
                    </b-navbar-nav>

                    <!-- Right aligned nav items -->
                    <b-navbar-nav class="ml-auto">
                        <b-navbar-brand>Create Your Own Okta Org ==></b-navbar-brand>
                        <b-nav-form>
                            <label class="sr-only" for="firstName">Name</label>
                            <b-input class="mb-2 mr-sm-2 mb-sm-0" id="firstName" placeholder="Jane" v-model="form.firstName"/>

                            <label class="sr-only" for="lastName">Name</label>
                            <b-input class="mb-2 mr-sm-2 mb-sm-0" id="lastName" placeholder="Doe" v-model="form.lastName"/>


                            <label class="sr-only" for="email">Email</label>
                            <b-input class="mb-2 mr-sm-2 mb-sm-0" id="email" placeholder="jane.doe@email.com" type="email" v-model="form.email"/>

                            <b-button variant="danger" v-on:click="setupChallenge">Get Started</b-button>
                        </b-nav-form>

                    </b-navbar-nav>
                </b-collapse>
            </b-navbar>
        </div>

        <b-container>
            <b-row align-h="center" v-if="!org.domain && errors.length">
                <b-col cols="8">
                    <p/>
                    <b-card header="Please correct the following error(s):">
                        <b-card-text>
                            <ul>
                                <li v-for="error in errors">{{error}}</li>
                            </ul>
                        </b-card-text>
                    </b-card>
                </b-col>
            </b-row>
            <b-row align-h="center">
                <b-col cols="8">
                    <div v-if="busy">
                        <h4><img src="../assets/fidget.gif" width="50px">{{busyMsg}}</h4>
                        <b-progress :value="counter" :max="max" show-progress animated />
                    </div>
                </b-col>
            </b-row>
            <p/>
        </b-container>
        <b-container v-if="org.domain">
            <p/>
            <b-row>
                <b-col><h2 class="text-center">Here's the information for your Okta org:</h2></b-col>
            </b-row>
            <b-row>
                <b-col><h4 class="text-center">(NOTE: This is the only time this will be shown)</h4></b-col>
            </b-row>
            <b-row align-h="center">
                <b-col cols="8">
                    <table class="table table-striped table-hover">
                        <tbody>
                        <tr>
                            <th scope="row">domain:</th>
                            <td>{{org.domain}}</td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>admin email:</nobr></th>
                            <td>{{org.adminEmail}}</td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>admin password:</nobr></th>
                            <td>{{org.adminPassword}}</td>
                        </tr>
                        <tr>
                            <th scope="row"><nobr>api token:</nobr></th>
                            <td>{{org.apiToken}}</td>
                        </tr>
                        </tbody>
                    </table>
                </b-col>
            </b-row>
            <b-row>
                <b-col class="text-center">
                    <b-button variant="primary" v-b-modal.postmanModal>Download Postman Collection</b-button>
                </b-col>
            </b-row>
            <p/>
        </b-container>
        <div>
            <b-card no-body>
                <b-tabs pills card>
                    <b-tab title="Tier 1: Earn a T-Shirt" active>
                        <Tier1/>
                    </b-tab>
                    <b-tab title="Tier 2: Earn a Hoodie">
                        <Tier2/>
                    </b-tab>
                </b-tabs>
            </b-card>
        </div>
        <div>
            <b-modal no-close-on-esc no-close-on-backdrop hide-header-close
                 size="huge" id="postmanModal" title="Caution!" @ok="getPostmanCollection"
            >
                <p>
                    Before proceeding, make sure that you've saved all the information about your org.<br/>
                    This information will be cleared from the browser after you've downloaded the
                    postman collection.
                </p>
                <table class="table table-striped">
                    <tbody>
                        <tr>
                            <th scope="row">domain:</th>
                            <td>{{org.domain}}</td>
                        </tr>
                        <tr>
                            <th scope="row">admin email::</th>
                            <td>{{org.adminEmail}}</td>
                        </tr>
                        <tr>
                            <th scope="row">admin password:</th>
                            <td>{{org.adminPassword}}</td>
                        </tr>
                        <tr>
                            <th scope="row">api token:</th>
                            <td>{{org.apiToken}}</td>
                        </tr>
                    </tbody>
                </table>
            </b-modal>
        </div>
        <div>
            <b-modal size="huge" ref="errorModal" title="Oops!">
                <p>
                    Something's gone horribly wrong!<br/>
                    Try again...
                </p>
                <p>
                    If this happens again, let our staff know.
                </p>
            </b-modal>
        </div>
    </div>
</template>

<script>
    import appConfig from '../.app.config'
    import orgScoreStore from '../model/orgScoreStore'
    import axios from 'axios'

    import Tier1 from '@/components/Tier1'
    import Tier2 from '@/components/Tier2'

    export default {
        name: 'hello',
        components: {
          Tier1, Tier2
        },
        data() {
            return {
                name: appConfig.event.name,
                errors: [],
                form: {
                    firstName: '',
                    lastName: '',
                    email: ''
                },
                busy: false,
                busyMsg: '',
                org: {},
                counter: 0,
                max: 100,
                scores: orgScoreStore.state.scores,
                orgName: orgScoreStore.state.org
            }
        },
        computed: {
          orgScore: function () {
              if (!this.scores.READ_ONLY_API_TOKEN) { return undefined }
              var score = 0
              Object.values(this.scores).forEach(value => score += value)
              return score
          }
        },
        methods: {
            clearData: function () {
              this.errors = []
              this.form.firstName = ''
              this.form.lastName = ''
              this.form.email = ''
              this.org = {}
            },
            checkForm: function () {
                this.errors = []
                if (!this.form.firstName) {
                    this.errors.push("First name is required.")
                }
                if (!this.form.lastName) {
                    this.errors.push("Last name is required.")
                }
                if (!this.form.email) {
                    this.errors.push("Email is required.")
                } else if (!this.validEmail(this.form.email)) {
                    this.errors.push(this.form.email + ' is not a valid email address')
                }
                return !this.errors.length
            },
            validEmail: function (email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            },
            setupChallenge: async function () {
                if (!this.checkForm()) {
                    return;
                }
                this.busy = true
                orgScoreStore.reset()
                this.createOrg()
                    .then(this.setAdminPassword)
                    .then(this.createUser)
                    .then(this.registerForChallenge)
                    .then(this.createApiToken)
                    .then((response) => {
                        this.org.domain = response.data.subdomain + '.' + response.data.domain
                        this.org.adminEmail = response.data.adminEmail
                        this.org.adminPassword = response.data.adminPassword
                        this.org.apiToken = response.data.apiToken
                        this.busy = false
                        this.counter = 0
                        orgScoreStore.setOrgName(response.data.subdomain)
                    })
                    .catch((err) => {
                        console.error(err.response)
                        this.counter = 0
                        this.busy = false
                        this.$refs.errorModal.show()
                    })
            },
            createOrg: async function () {
                this.counter += 20
                this.busyMsg = 'Creating Okta Org...'
                return axios.post(
                    appConfig.resourceServer.orgUrl,
                    {
                        firstName: this.form.firstName,
                        lastName: this.form.lastName,
                        email: this.form.email
                    },
                    { withCredentials: true }
                )
            },
            setAdminPassword: async function () {
                this.counter += 20
                this.busyMsg = 'Setting Admin Password...'
                return axios.post(
                    appConfig.resourceServer.adminPasswordUrl,
                    {},
                    { withCredentials: true }
                )
            },
            createUser: async function () {
                this.counter += 20
                this.busyMsg = 'Creating User: ' +
                    this.form.firstName + ' ' + this.form.lastName + ' with Super Admin role...'
                return axios.post(
                    appConfig.resourceServer.userUrl,
                    {},
                    { withCredentials: true }
                )
            },
            registerForChallenge: async function () {
                this.counter += 20
                this.busyMsg = 'Registering for the Challenge...'
                return axios.post(
                    appConfig.resourceServer.challengeUrl,
                    {},
                    { withCredentials: true }
                )
            },
            createApiToken: async function () {
                this.counter += 20
                this.busyMsg = 'Creating API Token...'
                return axios.post(
                    appConfig.resourceServer.apiTokenUrl,
                    {},
                    { withCredentials: true }
                )
            },
            getPostmanCollection: async function () {
                return axios({
                    url: appConfig.resourceServer.postmanUrl,
                    method: 'GET',
                    responseType: 'blob',
                    withCredentials: true
                }).then((response) => {
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', 'postman.json'); //or any other extension
                    document.body.appendChild(link);
                    link.click();
                    this.busy = false
                }).then(() => {
                    this.clearData()
                }).catch((response) => {
                    console.log(response)
                    this.busy = false
                })
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    @media (min-width: 600px) {
        .modal .modal-huge {
            max-width: 800px !important;
        }
    }
    .accordion {
        margin-left: 100px;
        margin-right: 100px;
    }
</style>
