import Vue from 'vue'

export default {
    state: {
        org: {},
        scores: {}
    },
    reset: function () {
        delete this.state.org.name
        for (var member in this.state.scores) {
            delete this.state.scores[member]
        }
    },
    setScores: function (scores) {
        Object.keys(scores).forEach((key) => {
            Vue.set(this.state.scores, key, scores[key])
        })
    },
    setOrgName: function (orgName) {
        Vue.set(this.state.org, 'name', orgName)
    }
}